<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      :default-sort="{prop: 'date', order: 'descending'}"
    >
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="customerId"
        label="客户号"
      >
      </el-table-column>
      <el-table-column
        prop="contractId"
        label="合同编号"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="contractId"
        label="仓单编号（仓单总数）"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="currentPrice"
        label="当前价格"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="warningPrice"
        label="预警价格"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="pledgeRate"
        label="解除预警时间"
        :formatter="formatter"
      >
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";

export default {
  data() {
    return {
      pageType: 2,
      searchForm: {
        option: "",
        inputText: ""
      },
      programForm: {
        weight: "",
        ammount: ""
      },
      currentPage: 1,
      pageSize: 1,
      totalPage: 100,

      tableData: [
        {
          customerId: "hz-hzjylxmyyxgs-180926",
          customerName: "杭州久益隆鑫贸易有限公司",
          contractId: "al0260025533",
          currentPrice: "6,596,691.4",
          warningPrice: "123123",
          pledgeRate: "50",
          contactMember: "张三 17826833504"
        }
      ]
    };
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo() {
      console.log(this.searchForm.option + "+" + this.searchForm.inputText);
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    openDialog(row, type) {
      
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    formatter(row, column) {
      return row.pledgeRate + "%";
    }
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.value-warning {
  border-spacing: 0;
  border-collapse: collapse;
}

.value-warning tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.value-warning tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
.dialog-button {
  float: right;
}
</style>



